<template>
	<div>
		<el-dialog v-model="formVisible" :title="formTitle" width="50%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" label-width="$template2.back.add.form.base.labelWidth" :rules="rules">
				<el-row>
					<el-col :span="12">
						<el-form-item label="学号" prop="xuehao">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.xuehao?true:false"
								v-model="form.xuehao" 
								placeholder="请选择学号"
								@change="xuehaoChange">
								<el-option v-for="(item,index) in xuehaoLists" :label="item" :value="item">
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="学生姓名" prop="xueshengxingming">
							<el-input class="list_inp" v-model="form.xueshengxingming" placeholder="学生姓名"
								 type="text" 								:readonly="!isAdd||disabledForm.xueshengxingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="班级" prop="banji">
							<el-input class="list_inp" v-model="form.banji" placeholder="班级"
								 type="text" 								:readonly="!isAdd||disabledForm.banji?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="家长账号" prop="jiazhangzhanghao">
							<el-input class="list_inp" v-model="form.jiazhangzhanghao" placeholder="家长账号"
								 type="text" 								:readonly="!isAdd||disabledForm.jiazhangzhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="兴趣爱好" prop="xingquaihao">
							<el-input class="list_inp" v-model="form.xingquaihao" placeholder="兴趣爱好"
								 type="text" 								:readonly="!isAdd||disabledForm.xingquaihao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="身心健康" prop="shenxinjiankang">
							<el-input class="list_inp" v-model="form.shenxinjiankang" placeholder="身心健康"
								 type="text" 								:readonly="!isAdd||disabledForm.shenxinjiankang?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="留言时间" prop="liuyanshijian">
							<el-date-picker
								class="list_date"
								v-model="form.liuyanshijian"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.liuyanshijian?true:false"
								placeholder="请选择留言时间" />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="教师工号" prop="jiaoshigonghao">
							<el-input class="list_inp" v-model="form.jiaoshigonghao" placeholder="教师工号"
								 type="text" 								:readonly="!isAdd||disabledForm.jiaoshigonghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="教师姓名" prop="jiaoshixingming">
							<el-input class="list_inp" v-model="form.jiaoshixingming" placeholder="教师姓名"
								 type="text" 								:readonly="!isAdd||disabledForm.jiaoshixingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="学业情况" prop="xueyeqingkuang">
							<el-input v-model="form.xueyeqingkuang" placeholder="学业情况" type="textarea"
							:readonly="!isAdd||disabledForm.xueyeqingkuang?true:false"
							/>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="行为表现" prop="xingweibiaoxian">
							<el-input v-model="form.xingweibiaoxian" placeholder="行为表现" type="textarea"
							:readonly="!isAdd||disabledForm.xingweibiaoxian?true:false"
							/>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="留言内容" prop="liuyanneirong">
							<el-input v-model="form.liuyanneirong" placeholder="留言内容" type="textarea"
							:readonly="!isAdd||disabledForm.liuyanneirong?true:false"
							/>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="formModel_cancel" @click="closeClick">取消</el-button>
					<el-button class="formModel_confirm" type="primary" @click="save"
						>
						提交
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//基础信息
	const tableName = 'liuyanxiaoxi'
	const formName = '留言消息'
	//基础信息
	//form表单
	const form = ref({})
	const disabledForm = ref({
		xuehao : false,
		xueshengxingming : false,
		banji : false,
		jiazhangzhanghao : false,
		xueyeqingkuang : false,
		xingweibiaoxian : false,
		xingquaihao : false,
		shenxinjiankang : false,
		liuyanneirong : false,
		liuyanshijian : false,
		jiaoshigonghao : false,
		jiaoshixingming : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		xuehao: [
		],
		xueshengxingming: [
		],
		banji: [
		],
		jiazhangzhanghao: [
		],
		xueyeqingkuang: [
		],
		xingweibiaoxian: [
		],
		xingquaihao: [
		],
		shenxinjiankang: [
		],
		liuyanneirong: [
		],
		liuyanshijian: [
		],
		jiaoshigonghao: [
		],
		jiaoshixingming: [
		],
	})
	//表单验证
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//学号列表
	const xuehaoLists = ref([])
	//methods

	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//重置
	const resetForm = () => {
		form.value = {
			xuehao: '',
			xueshengxingming: '',
			banji: '',
			jiazhangzhanghao: '',
			xueyeqingkuang: '',
			xingweibiaoxian: '',
			xingquaihao: '',
			shenxinjiankang: '',
			liuyanneirong: '',
			liuyanshijian: '',
			jiaoshigonghao: '',
			jiaoshixingming: '',
		}
	}
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
			form.value.liuyanshijian = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '新增' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '查看' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '修改' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='xuehao'){
					form.value.xuehao = row[x];
					disabledForm.value.xuehao = true;
					continue;
				}
				if(x=='xueshengxingming'){
					form.value.xueshengxingming = row[x];
					disabledForm.value.xueshengxingming = true;
					continue;
				}
				if(x=='banji'){
					form.value.banji = row[x];
					disabledForm.value.banji = true;
					continue;
				}
				if(x=='jiazhangzhanghao'){
					form.value.jiazhangzhanghao = row[x];
					disabledForm.value.jiazhangzhanghao = true;
					continue;
				}
				if(x=='xueyeqingkuang'){
					form.value.xueyeqingkuang = row[x];
					disabledForm.value.xueyeqingkuang = true;
					continue;
				}
				if(x=='xingweibiaoxian'){
					form.value.xingweibiaoxian = row[x];
					disabledForm.value.xingweibiaoxian = true;
					continue;
				}
				if(x=='xingquaihao'){
					form.value.xingquaihao = row[x];
					disabledForm.value.xingquaihao = true;
					continue;
				}
				if(x=='shenxinjiankang'){
					form.value.shenxinjiankang = row[x];
					disabledForm.value.shenxinjiankang = true;
					continue;
				}
				if(x=='liuyanneirong'){
					form.value.liuyanneirong = row[x];
					disabledForm.value.liuyanneirong = true;
					continue;
				}
				if(x=='liuyanshijian'){
					form.value.liuyanshijian = row[x];
					disabledForm.value.liuyanshijian = true;
					continue;
				}
				if(x=='jiaoshigonghao'){
					form.value.jiaoshigonghao = row[x];
					disabledForm.value.jiaoshigonghao = true;
					continue;
				}
				if(x=='jiaoshixingming'){
					form.value.jiaoshixingming = row[x];
					disabledForm.value.jiaoshixingming = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('jiaoshigonghao')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.jiaoshigonghao = json.jiaoshigonghao
				disabledForm.value.jiaoshigonghao = true;
			}
			if(json.hasOwnProperty('jiaoshixingming')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.jiaoshixingming = json.jiaoshixingming
				disabledForm.value.jiaoshixingming = true;
			}
		})
		context?.$http({
			url: `option/xuesheng/xuehao`,
			method: 'get'
		}).then(res=>{
			xuehaoLists.value = res.data.data
		})
		//由上级字段带出不可改
		disabledForm.value.xueshengxingming = true;
		//由上级字段带出不可改
		disabledForm.value.banji = true;
		//由上级字段带出不可改
		disabledForm.value.jiazhangzhanghao = true;
	}
	//初始化
	//声明父级调用
	defineExpose({
		init
	})
	//关闭
	const closeClick = () => {
		formVisible.value = false
	}
	const xuehaoChange=()=>{
		context?.$http({
			url: `follow/xuesheng/xuehao?columnValue=` + form.value.xuehao,
			method: 'get'
		}).then(res=>{
			if(res.data.data.xueshengxingming){
				form.value.xueshengxingming = res.data.data.xueshengxingming
			}
			if(res.data.data.banji){
				form.value.banji = res.data.data.banji
			}
			if(res.data.data.jiazhangzhanghao){
				form.value.jiazhangzhanghao = res.data.data.jiazhangzhanghao
			}
		})
	}
	//富文本
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//提交
	const save=()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								emit('formModelChange')
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									formVisible.value = false
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						emit('formModelChange')
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							formVisible.value = false
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
</script>
<style lang="scss" scoped>
	// 表单
	.formModel_form{
		border: 0px solid #ddd;
		border-radius: 4px;
		padding: 30px;
		margin: 0;
		background: #fff;
		// form item
		:deep(.el-form-item) {
			margin: 0 150px 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 background: none;
			 font-weight: 500;
			 display: block;
			 width: 150px;
			 min-width: 150px;
			 text-align: right;
			}
			// 内容盒子
			.el-form-item__content {
				display: flex;
				width: calc(100% - 120px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 输入框
				.list_inp {
					border: 0px solid #eee;
					border-radius: 0px;
					padding: 0 10px;
					clip-path: polygon(98% 0, 100% 51%, 98% 100%, 2% 100%, 0% 50%, 2% 0);
					background: #eee;
					width: auto;
					line-height: 36px;
					box-sizing: border-box;
					min-width: 250px;
					height: 36px;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				//日期选择器
				.list_date {
					border: 0px solid #eee;
					border-radius: 0px;
					clip-path: polygon(98% 0, 100% 51%, 98% 100%, 2% 100%, 0% 50%, 2% 0);
					background: #eee;
					width: auto;
					line-height: 36px;
					box-sizing: border-box;
					min-width: 250px;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
					}
				}
				// 下拉框
				.list_sel {
					border: 0px solid #eee;
					border-radius: 0px;
					padding: 0 10px;
					clip-path: polygon(98% 0, 100% 51%, 98% 100%, 2% 100%, 0% 50%, 2% 0);
					background: #eee;
					width: auto;
					line-height: 36px;
					box-sizing: border-box;
					min-width: 250px;
					//去掉默认样式
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
							.el-input__wrapper{
								border: none;
								box-shadow: none;
								background: none;
								border-radius: 0;
								height: 100%;
								padding: 0;
							}
							.is-focus {
								box-shadow: none !important;
							}
						}
					}
				}
				// 长文本
				.el-textarea__inner {
					border: 0px solid #eee;
					border-radius: 0;
					padding: 12px 24px;
					clip-path: polygon(97% 0, 100% 53%, 97% 100%, 3% 100%, 0% 50%, 3% 0);
					color: #333;
					background: #eee;
					width: 100%;
					font-size: 14px;
					min-height: 120px;
				}
			}
		}
	}
	// 按钮盒子
	.formModel_btn_box {
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: center;
		.formModel_cancel {
			border: 0;
			cursor: pointer;
			border-radius: 10px;
			padding: 0 24px 0 34px;
			margin: 0 20px 0 0;
			clip-path: polygon(5% 0, 100% 0%, 95% 100%, 0% 100%);
			color: #fff;
			background: url(http://clfile.zggen.cn/20240410/189ad7c83d664e8ca2e346f3ad3382d6.png) no-repeat 15px center / 22px,#be696d;
			width: auto;
			font-size: 16px;
			min-width: 120px;
			height: 40px;
		}
		.formModel_cancel:hover {
		}
		
		.formModel_confirm {
			border: 0;
			cursor: pointer;
			border-radius: 10px;
			padding: 0 24px 0 34px;
			margin: 0 20px 0 0;
			clip-path: polygon(5% 0, 100% 0%, 95% 100%, 0% 100%);
			color: #fff;
