	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import qingjiashenqing from '@/views/qingjiashenqing/list'
	import huifuxinxi from '@/views/huifuxinxi/list'
	import xuesheng from '@/views/xuesheng/list'
	import examquestion from '@/views/exam/examquestion/list'
	import wenjuandiaocha from '@/views/wenjuandiaocha/list'
	import syslog from '@/views/syslog/list'
	import xueshengchengji from '@/views/xueshengchengji/list'
	import ketangbiaoxian from '@/views/ketangbiaoxian/list'
	import kechengbiao from '@/views/kechengbiao/list'
	import banjixinxi from '@/views/banjixinxi/list'
	import kechengzuoye from '@/views/kechengzuoye/list'
	import zuoyepigai from '@/views/zuoyepigai/list'
	import jiaoshi from '@/views/jiaoshi/list'
	import huodongxinxi from '@/views/huodongxinxi/list'
	import fankuiyijian from '@/views/fankuiyijian/list'
	import exampaper from '@/views/exampaper/list'
	import users from '@/views/users/list'
	import jiazhang from '@/views/jiazhang/list'
	import huodongbaoming from '@/views/huodongbaoming/list'
	import tongzhigonggao from '@/views/tongzhigonggao/list'
	import tijiaozuoye from '@/views/tijiaozuoye/list'
	import meiricanpu from '@/views/meiricanpu/list'
	import wenjianguanli from '@/views/wenjianguanli/list'
	import liuyanxiaoxi from '@/views/liuyanxiaoxi/list'
	import exampaperlist from '@/views/exam/exampaperlist/list'
	import examination from '@/views/exam/exampaperlist/examination'
	import examrecord from '@/views/exam/examrecord/list'
	import examfailrecord from '@/views/exam/examfailrecord/list'
	import xueshengRegister from '@/views/xuesheng/register'
	import xueshengCenter from '@/views/xuesheng/center'
	import jiazhangRegister from '@/views/jiazhang/register'
	import jiazhangCenter from '@/views/jiazhang/center'
	import jiaoshiRegister from '@/views/jiaoshi/register'
	import jiaoshiCenter from '@/views/jiaoshi/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/xueshengCenter',
			name: '学生个人中心',
			component: xueshengCenter
		}
		,{
			path: '/jiazhangCenter',
			name: '家长个人中心',
			component: jiazhangCenter
		}
		,{
			path: '/jiaoshiCenter',
			name: '教师个人中心',
			component: jiaoshiCenter
		}
		,{
			path: '/qingjiashenqing',
			name: '请假申请',
			component: qingjiashenqing
		}
		,{
			path: '/huifuxinxi',
			name: '回复信息',
			component: huifuxinxi
		}
		,{
			path: '/xuesheng',
			name: '学生',
			component: xuesheng
		}
		,{
			path: '/examquestion',
			name: '试题管理',
			component: examquestion
		}
		,{
			path: '/wenjuandiaocha',
			name: '问卷调查',
			component: wenjuandiaocha
		}
		,{
			path: '/syslog',
			name: '操作日志',
			component: syslog
		}
		,{
			path: '/xueshengchengji',
			name: '学生成绩',
			component: xueshengchengji
		}
		,{
			path: '/ketangbiaoxian',
			name: '课堂表现',
			component: ketangbiaoxian
		}
		,{
			path: '/kechengbiao',
			name: '课程表',
			component: kechengbiao
		}
		,{
			path: '/banjixinxi',
			name: '班级信息',
			component: banjixinxi
		}
		,{
			path: '/kechengzuoye',
			name: '课程作业',
			component: kechengzuoye
		}
		,{
			path: '/zuoyepigai',
			name: '作业批改',
			component: zuoyepigai
		}
		,{
			path: '/jiaoshi',
			name: '教师',
			component: jiaoshi
		}
		,{
			path: '/huodongxinxi',
			name: '活动信息',
			component: huodongxinxi
		}
		,{
			path: '/fankuiyijian',
			name: '反馈意见',
			component: fankuiyijian
		}
		,{
			path: '/exampaper',
			name: '试卷列表',
			component: exampaper
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		,{
			path: '/jiazhang',
			name: '家长',
			component: jiazhang
		}
		,{
			path: '/huodongbaoming',
			name: '活动报名',
			component: huodongbaoming
		}
		,{
			path: '/tongzhigonggao',
			name: '通知公告',
			component: tongzhigonggao
		}
		,{
			path: '/tijiaozuoye',
			name: '提交作业',
			component: tijiaozuoye
		}
		,{
			path: '/meiricanpu',
			name: '每日餐谱',
			component: meiricanpu
		}
		,{
			path: '/wenjianguanli',
			name: '文件管理',
			component: wenjianguanli
		}
		,{
			path: '/liuyanxiaoxi',
			name: '留言消息',
			component: liuyanxiaoxi
		}
		, {
			path: '/exampaperlist',
			name: '考试列表',
			component: exampaperlist
		}, {
			path: '/examrecord',
			name: '考试记录',
			component: examrecord
		}, {
			path: '/examfailrecord',
			name: '错题本',
			component: examfailrecord
		}
		]
	},
	{
		path: '/xueshengRegister',
		name: '学生注册',
		component: xueshengRegister
	},
	{
		path: '/jiazhangRegister',
		name: '家长注册',
		component: jiazhangRegister
	},
	{
		path: '/jiaoshiRegister',
		name: '教师注册',
		component: jiaoshiRegister
	},
	{
		path: '/examination',
		name: '考试',
		component: examination
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
